import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import uniTag from '@/components/uni-tag/uni-tag.vue'
import uniIcons from '@/components/uni-icons/uni-icons.vue'
import auditIdea from '@/pages/index/audit-idea.vue'
import {
	filePreview,
	formatTaskStatus
} from '@/common/utils/index.js'
import {
	formatDate
} from '@/common/utils/datetime.js'
import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	components: {
		uniGoodsNav,
		uniTag,
		uniIcons,
		auditIdea
	},
	computed: {
		...mapState([
			'selectOrder'
		])
	},
	data() {
		return {
			isPass: false,
			item: {},
			options: [],
			buttonGroup: [{
					text: '返回',
					backgroundColor: '#333',
					color: '#fff'
				},
				{
					text: '执行',
					backgroundColor: '#39b54a',
					color: '#fff'
				}
			],
			opinionList: []
		}
	},
	onLoad(options) {
		try {
			if (options.data == this.selectOrder.id) {
				this.item = this.selectOrder
				this.getTicketHistory(this.item.ticketNo)
			}
		} catch (e) {
			this.item = {}
		}
	},
	methods: {
		formatDate,
		filePreview,
		formatTaskStatus,
		getTicketHistory(ticketNo) {
			this.$api.getByTicketNo(ticketNo).then(res => {
				this.opinionList = res.data
			})
		},
		onClick(e) {
			// uni.showToast({
			// 	title: `点击${e.content.text}`,
			// 	icon: 'none'
			// })
			if (e.content.text === '调整') {
				uni.navigateTo({
					url: '/pages/index/project/adjust-project?data=' + JSON.stringify(this.item)
				})
			}
		},
		buttonClick(e) {
			if (e.index === 0) {
				uni.navigateBack({
					delta: 1
				})
			} else if (e.index === 1) {
				uni.navigateTo({
					url: '/pages/index/order/adjust-order?data=' + this.item.id
				})
			}
		},
		updateQuery() {
			// 更新数据
			let pages = getCurrentPages()
			// let currPage = pages[pages.length - 1]; //当前页面
			let prevPage = pages[pages.length - 2] //上一个页面
			prevPage.isDoRefresh = true
			setTimeout(() => {
				uni.navigateBack({
					delta: 1
				})
			}, 1000)
		}
	}
}
